import { AxiosRequestConfig, BaseResponse } from "@/services/types";
import { $api, configAppJson } from "@/services/constant";
import { HOST_API_V1 } from "@/services/env-init";
import {
  CatPembahasan,
  CatResult,
  DataCat,
  PayloadSubmitCat,
  ResponseGetPassingGrade,
  ResponseListCatCpns,
} from "./types";

const BASE_URL_MEMBER_CAT = HOST_API_V1 + "member/cat";

export const getMemberCatQuestion = async (
  jenis: string,
  type: string,
  config?: AxiosRequestConfig
): Promise<BaseResponse<DataCat>> => {
  const res = await $api().get(
    `${BASE_URL_MEMBER_CAT}/question/${jenis}/${type}`,
    config
  );
  return res.data;
};

export const postMemberCatSubmit = async (
  kodeExam: string,
  payload: PayloadSubmitCat
) => {
  const config = configAppJson();

  const res = await $api().post(
    `${BASE_URL_MEMBER_CAT}/submit/${kodeExam}`,
    payload,
    config
  );

  return res.data;
};

export const getMemberCatResult = async (
  kodeExam: string
): Promise<BaseResponse<CatResult>> => {
  const res = await $api().get(`${BASE_URL_MEMBER_CAT}/result/${kodeExam}`);
  return res.data;
};

export const getMemberCatPembahasan = async (
  kodeExam: string
): Promise<BaseResponse<CatPembahasan>> => {
  const res = await $api().get(`${BASE_URL_MEMBER_CAT}/pembahasan/${kodeExam}`);
  return res.data;
};

export const getMemberJenisCat = async (
  type: string
): Promise<BaseResponse<ResponseListCatCpns[]>> => {
  const res = await $api().get(`${BASE_URL_MEMBER_CAT}/jenis/${type}`);
  return res.data;
};

export const getMemberCatPassingGrade = async (
  type: string
): Promise<BaseResponse<ResponseGetPassingGrade>> => {
  const res = await $api().get(`${BASE_URL_MEMBER_CAT}/passing-grade/${type}`);
  return res.data;
};

export const getMemberCatHistory = async (
  config: AxiosRequestConfig
): Promise<BaseResponse<any>> => {
  const res = await $api().get(`${BASE_URL_MEMBER_CAT}/history`, config);
  return res.data;
};
